import './WestForm.scss'
import React, { useRef } from 'react';
import { motion } from 'framer-motion';
import emailjs from '@emailjs/browser';

function WestForm() {
    const form = useRef();

    const sendEmail = (e) => {
        e.preventDefault();
  
      emailjs.sendForm('service_fmre69r', 'form_without_name', form.current, 'd8OBmhhtk0a-n4BwO')
        .then((result) => {
            console.log(result.text);
            window.location.reload()
        }, (error) => {
            console.log(error.text);
            alert('ERROR')
        });
        window.scrollTo(0, 0)
    };
    return (
        <form ref={form} className='termination' onSubmit={sendEmail}>
            <div className='west-form'>
                <motion.input whileHover={{scale: 1.03}} className="west-form__input form-input" type="text" name="name" placeholder=" '" required/>
                <motion.input whileHover={{scale: 1.03}} className="west-form__input form-input" type="tel" name="phone" maxLength="13" placeholder=" " required/>

                <motion.select whileHover={{scale: 1.03}} className='west-form__input form-input select' name='house'>
                    <option value='' selected disabled> </option>
                    <option value=''></option>
                    <option value=''></option>
                    <option value=' '> </option>
                </motion.select>

                <motion.select whileHover={{scale: 1.03}} className='west-form__input form-input select' name='messanger'>
                    <option value='' selected disabled>,    </option>
                    <option value='Viber'>Viber</option>
                    <option value='Telegram'>Telegram</option>
                    <option value='Wats App'>WatsApp</option>
                </motion.select>

                <motion.button whileHover={{scale: 1.03}} className="west-form__button main-button"></motion.button>
            </div>
        </form>
    )
}

export default WestForm