import React, { useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination } from 'swiper/modules';
import sprite from '../assets/icons/sprite.svg'
import FsLightbox from 'fslightbox-react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import './ComplexSlider.css';

import beskidImg1 from '../assets/images/beskid-home/0.jpg'
import beskidImg2 from '../assets/images/beskid-home/1.jpg'
import beskidImg3 from '../assets/images/beskid-home/2.jpg'
import beskidImg4 from '../assets/images/beskid-home/3.jpg'
import beskidImg5 from '../assets/images/beskid-home/4.jpg'
import beskidImg6 from '../assets/images/beskid-home/5.jpg'
import beskidImg7 from '../assets/images/beskid-home/6.jpg'
import beskidImg8 from '../assets/images/beskid-home/7.jpg'
import beskidImg9 from '../assets/images/beskid-home/8.jpg'
import beskidImg10 from '../assets/images/beskid-home/9.jpg'
import beskidImg11 from '../assets/images/beskid-home/10.jpg'
import beskidImg12 from '../assets/images/beskid-home/11.jpg'
import beskidImg13 from '../assets/images/beskid-home/12.jpg'
import beskidImg14 from '../assets/images/beskid-home/13.jpg'
import beskidImg15 from '../assets/images/beskid-home/14.jpg'
import beskidImg16 from '../assets/images/beskid-home/15.jpg'

const imgArr = [
  {
    src: 'besked1',
    alt: 'beskid-home'
  },
  {
    src: 'besked2',
    alt: 'beskid-home'
  },
  {
    src: 'besked3',
    alt: 'beskid-home'
  },
  {
    src: 'besked4',
    alt: 'beskid-home'
  },
  {
    src: 'besked5',
    alt: 'beskid-home'
  },
  {
    src: 'besked6',
    alt: 'beskid-home'
  },
  {
    src: 'besked7',
    alt: 'beskid-home'
  },
  {
    src: 'besked8',
    alt: 'beskid-home'
  },
  {
    src: 'besked9',
    alt: 'beskid-home'
  },
  {
    src: 'besked10',
    alt: 'beskid-home'
  },
  {
    src: 'besked11',
    alt: 'beskid-home'
  },
  {
    src: 'besked12',
    alt: 'beskid-home'
  },
  {
    src: 'besked13',
    alt: 'beskid-home'
  },
  {
    src: 'besked14',
    alt: 'beskid-home'
  },
  {
    src: 'besked15',
    alt: 'beskid-home'
  },
  {
    src: 'besked16',
    alt: 'beskid-home'
  },
  {
    src: 'besked17',
    alt: 'beskid-home'
  },
]

export default function Slider() {
  const [isActive, setIsActive] = useState(true)

  const handleClick = () => {
      setIsActive(!isActive)
  }
  const buttonClassActive = isActive ? "main-button type__button type__button--active" : "main-button type__button"
  const buttonClass = isActive ? "main-button type__button" : "main-button type__button type__button--active"
  const squareContent = isActive ? '117,94' : '121,27'
  const terraceContent = isActive ? '16,80' : '27,90'

  const [toggler, setToggler] = useState(false)

  const handleClickScroll = () => {
    window.scrollTo(0, 0)
  }
  return (
    <>
    <div className="complex__info box">
        <div className='complex__info-slider'>
          <Swiper
            pagination={{
              dynamicBullets: true,
            }}
            navigation={true}
            modules={[Pagination, Navigation]}
            style={{
              "--swiper-navigation-color": "#fff",
              "--swiper-navigation-size": "8px",
            }}
            className="mySwiper"
          >

            {imgArr.map((item, index) => {
              return (
                <SwiperSlide onClick={() => {setToggler(!toggler)}}>
                  <img className="slider-img" src={require('../assets/images/beskid-home/' + index + '.jpg')} alt={item.alt}/><div className="shadow"></div>
                </SwiperSlide>
              )
            })}
            
          </Swiper>
        </div>

        <div className="complex__info-text complex__base-card">
          <h3 className="info__title box">BESKID HOME RESORT (BUKOVEL)</h3>

          <p className='info__sub-title box'>   .,   (BUKOVEL).</p>

          <div className="complex__info-types box">
              <button className={buttonClassActive} id="typeButton-one" onClick={handleClick}> - 1</button>
              <button className={buttonClass} id="typeButton-two" onClick={handleClick}> - 2</button>
          </div>

          <div className="complex__text-container besket_text box">
              <div className="sizes-container">
                  <p className="info__text"> -  - {squareContent} 2</p>
                  <p className="info__text"> -   {terraceContent} 2</p>
              </div>

              <div className="info__container box">
                  <p className="info-text">
                            
                          ,    .  
                        -  ,      
                      .           .
                  </p>
              </div>

              {/*=================== card actions ========================*/}
              
              <div className='info-actions'>
                  <a href='#beskid-home'>
                  <button className='info-actions__btn' onClick={handleClickScroll}>
                        
                      <svg className="btn-icon">
                          <use href={sprite + '#chevron-right'}/>
                      </svg>
                  </button>
                  </a>
              </div>
          </div>
        </div>
      </div>

      <FsLightbox
        toggler={toggler}
        sources={[
          beskidImg1, 
          beskidImg2,
          beskidImg3,
          beskidImg4,
          beskidImg5,
          beskidImg6,
          beskidImg7,
          beskidImg8,
          beskidImg9,
          beskidImg10,
          beskidImg11,
          beskidImg12,
          beskidImg13,
          beskidImg14,
          beskidImg15,
          beskidImg16,
        ]}
      />
    </>
  );
}

